@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
.container{
    display: flex;
    flex-direction: column;
}

.timerContainer{
    position: relative;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
}

.reportButtonContainer{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 4rem;
    margin-top: 6.5rem;
    min-height: 32px;
}

.timerMain{
    width: 30rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #79B6FD;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 3rem;
    padding-right: 3rem;
    border-radius: 5%;
}

.timerControlButton{
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-content: space-around;
}

.timerCountdown{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: fit;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.timerEstimated{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.timerCenter{
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 25rem;
    justify-content: space-around;
    padding-top: 2rem;
}

.startButton{
    background-color: #ABE9CD;
    border: #ABE9CD;
    border-radius: 1rem;
    cursor: pointer;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 2rem;
    color: #FFFFFF;
    outline: none;
    box-shadow: 0 9px #999;

}

.stopButton{
    background-color: #DD7554;
    border: #DD7554;
    border-radius: 1rem;
    cursor: pointer;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 2rem;
    color: #FFFFFF;
    outline: none;
    box-shadow: 0 9px #999;
}

.stopButton:active, .stopButton:focus {
    outline:0;
    opacity: .5;
    background-color: #DD7554;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.startButton:active, .startButton:focus {
    outline:0;
    opacity: .5;
    background-color: #95CAB2;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.startButton:active, .startButton:focus {
    outline:0;
    opacity: .5;
    background-color: #95CAB2;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.time {
    font-size: 9rem;
    font-weight: 600;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    color: #fff;
    display: block;
    width: 100%;
    text-align: center;
  }
.container{
    /* height: 15vh; */
    /* width: 24vh; */
}

.componentContainer{
    height: 9rem;
    width: 12rem;
    display: flex;
    flex-direction: column;
    border-radius: 8%;
    background-color: #FFE68E;
}

.componentContainerTop{
    height: 7rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-content: center;
}

.componentContainerTopIcon{
    display: flex;
    justify-content: center;
    align-content: center;
    align-self: center;
}

.componentContainerTopLabel{
    font-size:5rem;
    font-weight: 600;
    color: #003E73;
    outline: black;
}

.componentContainerBottom{
    height: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.componentContainerBottomLabel{
    font-family: Roboto;
    font-weight: 500;
}

.bar{
    display: flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 2.5rem;
}

.barInnerContainer{
    width: 60rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    height: fit-content;
    flex-direction: row;
    justify-content: space-evenly;
}

.focusHourBar{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 5rem;
    padding-left: 5rem;
    padding-right: 5rem;
}

.focusHourFont{
    font-family: 'Roboto Slab', serif;
    font-size: 1.5rem;
    color: #003E73;
    text-decoration: underline;
}
* > body {
    margin: 0;
  }

/*navbar*/
#navbar {
    top:0; left:0; right:0;
    background-color: #C1DEFF;
    z-index: 100;
    color: black;
    box-shadow: none;
}

/*aligning navbar elements*/
#navflex {
    display: flex;
    justify-content: space-between;
}

/*flex direction of nav link lists*/
#navlink{
    display: flex;
    flex-direction: row;
}
